import React, { Fragment, useEffect } from "react";
import mernstac1 from "../../styles/assets/images/mernstack/mernstac1.jpg";
import mernstac2 from "../../styles/assets/images/mernstack/mernstac2.jpg";
import mernstac3 from "../../styles/assets/images/mernstack/mernstac3.jpg";
import { useState } from "react";
import { Container, Row, Col, Carousel, Card } from "react-bootstrap";

const MernDevelopmentServices = () => {
  let currentIndex = 0;
  let isPaused = false;
  const [idx, setIdx] = useState("");

  function showSlide(index) {
    const carouselItems = document.querySelectorAll(".carousel-item");
    if (index < 0) {
      index = carouselItems.length - 1;
    } else if (index >= carouselItems.length) {
      index = 0;
    }
    setIdx(index);
    currentIndex = index;
  }

  function autoSlide() {
    showSlide(currentIndex + 1);
  }

  useEffect(() => {
    const carousel = document.querySelector(".carousel");
    carousel?.addEventListener("mouseover", () => {
      isPaused = true;
    });

    carousel?.addEventListener("mouseout", () => {
      isPaused = false;
    });

    setInterval(autoSlide, 3000);
  }, []);

  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-5 row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  MERN/MEAN Stack Development Services
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  At our company, we specialize in providing top-notch MERN/MEAN
                  stack development services. Our team of experienced developers
                  is well-versed in the latest technologies and tools to build
                  scalable and high-performance web applications that meet your
                  specific business needs.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <main>
        <div className="container merncarousel">
          <div className="d-flex align-items-center row">
            <div className="col-md-12">
              <Carousel>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="fs-5 fw-semibold p-5">
                        <p className="fs-4 fw-semibold">
                          At our company, we specialize in providing top-notch
                          MERN/MEAN stack development services. Our team of
                          experienced developers is well-versed in the latest
                          technologies and tools to build scalable and
                          high-performance web applications that meet your
                          specific business needs.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac1}
                        alt="mernstack-1-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          Our MERN stack development services include the use of
                          MongoDB, Express.js, React, and Node.js, while our
                          MEAN stack development services include the use of
                          MongoDB, Express.js, AngularJS, and Node.js. Both
                          stacks are built using JavaScript, making them highly
                          efficient and easy to maintain.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac2}
                        alt="mernstack-2-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          We understand the importance of building web
                          applications that are responsive, user-friendly, and
                          optimized for search engines. That's why we use
                          popular SEO keywords to ensure that your website ranks
                          well on search engines, driving more traffic and
                          conversions.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac3}
                        alt="mernstack-3-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </div>
      </main>
    </Fragment>
  );
};

export default MernDevelopmentServices;
